@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);

html { 
    font-family: 'Roboto', sans-serif !important;
    scroll-behavior: smooth;
}


.container {
  width: 96%;
  margin: 0 auto;
}

@media screen and (min-width: 768px){
  .container {
      width: 720px;
  }
}

@media screen and (min-width: 1200px){
  .container {
      width: 960px;
  }
}
  
.main-title-carousel{margin-bottom:20px;font-weight:bold;font-family:'Apple Chancery', cursive;color:white;font-size:22pt}.main-desc-text{font-size:10pt}.carosuel-photo{width:100%;height:90vh;object-fit:cover}.shadow-box{position:absolute;height:90vh;width:100%;background:rgba(0,0,0,0.6)}.carousel-content{height:90vh;display:flex;flex-direction:column;justify-content:center;align-items:center;padding-top:100px}@media screen and (min-width: 768px){.main-title-carousel{font-size:25pt}.main-desc-text{font-size:11pt}}@media screen and (min-width: 1200px){.main-title-carousel{margin-bottom:30px;font-size:35pt}.main-desc-text{font-size:13pt}.carosuel-photo{object-fit:inherit}}

header{position:relative;display:flex;justify-content:center;align-items:center;height:60px;background:rgba(0,0,0,0.8)}header h3{margin:0 10px;font-size:20px;color:#9b9a9a}header img{-webkit-filter:opacity(80%);filter:opacity(80%)}.transform-img{-webkit-transform:scaleX(-1);transform:scaleX(-1)}

#menu-container{height:80px;background:#212020}#menu-container nav{margin-right:50px;height:100%}#multi-drop-menu ul{display:flex;justify-content:flex-end;align-items:center;list-style-type:none;height:100%}#multi-drop-menu a{text-decoration:none}#multi-drop-menu ul{list-style:none;margin:0;padding-left:0}#multi-drop-menu li{color:#fff;display:block;padding:1rem;position:relative;text-decoration:none;-webkit-transition-duration:0.5s;transition-duration:0.5s}#multi-drop-menu li a{color:#fff}#multi-drop-menu li a:hover{color:#817b1e}#multi-drop-menu li:hover,#multi-drop-menu li:focus-within{cursor:pointer}#multi-drop-menu li:focus-within a{outline:none}#multi-drop-menu ul li ul{visibility:hidden;opacity:0;min-width:5rem;position:absolute;-webkit-transition:all 0.5s ease;transition:all 0.5s ease;margin-top:19px;margin-left:-17px;display:none}#multi-drop-menu ul li:hover>ul,#multi-drop-menu ul li:focus-within>ul,#multi-drop-menu ul li ul:hover,#multi-drop-menu ul li ul:focus{visibility:visible;opacity:1;display:block;z-index:1000}#multi-drop-menu ul li ul li{background:#212020;clear:both;width:100%;padding-right:200px}.big-dot{display:inline-block;height:8px;width:8px;background-color:#817b1e;border-radius:50%;margin:0 10px}

.main-title{height:130px;background:darkgreen;-webkit-clip-path:polygon(0 0, 100% 0, 100% 40%, 0 100%);clip-path:polygon(0 0, 100% 0, 100% 40%, 0 100%)}.main-title h2{position:relative;padding-top:20px;font-family:'Apple Chancery', cursive;font-weight:bold;font-size:55px;color:white;font-style:italic}.describe{margin-top:30px;margin-bottom:30px}.describe div{font-size:18px;text-indent:50px;text-align:justify;text-justify:inter-word}.describe div span{text-align:center;font-weight:bold}.describe div span:nth-child(2){display:block;font-size:22px}.describe div span:last-child{text-decoration:underline}.describe div:last-child{text-align:center;font-weight:bold}

