html { 
    font-family: 'Roboto', sans-serif !important;
    scroll-behavior: smooth;
}


.container {
  width: 96%;
  margin: 0 auto;
}

@media screen and (min-width: 768px){
  .container {
      width: 720px;
  }
}

@media screen and (min-width: 1200px){
  .container {
      width: 960px;
  }
}
  